import React, { Component } from 'react';
import { Link } from 'react-router';
import isEmpty from 'lodash/isEmpty';
import cx from 'classnames';
import './ProjectHeader.scss';

import GridHelper from './GridHelper';

class ProjectHeader extends Component {
  render() {
    const isConfidential = this.props.isConfidential;
    const hasLink = !isEmpty(this.props.link);
    const style = {
      top: this._getRandomArbitrary(10, 20)+'%',
      transform: 'translateX('+this._getRandomArbitrary(-20, 10)+'%) rotate('+this._getRandomArbitrary(-4, 1)+'deg)',
      backgroundSize: this._getRandomArbitrary(100, 120)+'% 70%',
    }
    const confidentialStyle = {
      width: this._getRandomArbitrary(105, 130)+'%',
      transform: 'translateX('+this._getRandomArbitrary(-15, -5)+'%) rotate('+this._getRandomArbitrary(-1.5, 0.5)+'deg)',
    }
    const className = cx({
      'ProjectHeader': true,
      'ProjectHeader-Confidential': isConfidential
    });
    console.log(className);
    return (
      <div className={className}>
        {hasLink && <Link to={this.props.link.to}></Link>}
        <h2>{this.props.title}<span style={isConfidential ? confidentialStyle : (hasLink ? style : {})}></span></h2>
        <p>{this.props.caption}</p>
      </div>
    );
  }

  _getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
  }
}

ProjectHeader.propTypes = {
  link: React.PropTypes.object,
  title: React.PropTypes.string.isRequired,
  caption: React.PropTypes.string,
  isConfidential: React.PropTypes.bool
};
ProjectHeader.defaultProps = {};

export default ProjectHeader;



